@-webkit-keyframes upDown {
  0% {
    bottom: 30%;
    left: 20%; }
  50% {
    bottom: 33%;
    left: 20%; } }

@keyframes upDown {
  0% {
    bottom: 30%;
    left: 20%; }
  50% {
    bottom: 33%;
    left: 20%; } }

@-webkit-keyframes flow {
  100% {
    background-position: 0 -100000%; } }

@keyframes flow {
  100% {
    background-position: 0 -100000%; } }

@-webkit-keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@-webkit-keyframes forward {
  from {
    background-position: 0 0; }
  to {
    background-position: 0 2000%; } }

@keyframes forward {
  from {
    background-position: 0 0; }
  to {
    background-position: 0 2000%; } }

.octo-forward {
  transition: -webkit-transform 2.5s ease-in-out;
  transition: transform 2.5s ease-in-out;
  transition: transform 2.5s ease-in-out, -webkit-transform 2.5s ease-in-out;
  -webkit-transform: translate(100vw, 0);
          transform: translate(100vw, 0); }

.lighten-background {
  opacity: .5; }

.ship-position-1 {
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0); }

.ship-position-2 {
  -webkit-transform: translate(10vw, 0);
          transform: translate(10vw, 0); }

.ship-position-3 {
  -webkit-transform: translate(22vw, 0);
          transform: translate(22vw, 0); }

.explode {
  -webkit-transform: scale(2);
          transform: scale(2); }

.ship-position-4 {
  -webkit-transform: translate(34vw, 0);
          transform: translate(34vw, 0); }

.ship-position-5 {
  -webkit-transform: translate(46vw, 0);
          transform: translate(46vw, 0); }

.obstacle-pos-10 {
  left: 0; }

.obstacle-pos-20 {
  left: 20%; }

.obstacle-pos-30 {
  left: 40%; }

.obstacle-pos-40 {
  left: 60%; }

.obstacle-pos-50 {
  left: 80%; }

.obstacle-pos-60 {
  left: 40%;
  top: 30%; }

.obstacle-pos-11 {
  left: 0;
  top: 20%; }

.obstacle-pos-21 {
  left: 20%;
  top: 10%; }

.obstacle-pos-31 {
  left: 40%;
  top: 15%; }

.obstacle-pos-41 {
  left: 60%;
  top: 13%; }

.obstacle-pos-51 {
  left: 80%;
  top: 19%; }

.obstacle-pos-61 {
  left: 20%;
  top: 30%; }

.obstacles-move {
  transition: -webkit-transform 3s ease-in-out;
  transition: transform 3s ease-in-out;
  transition: transform 3s ease-in-out, -webkit-transform 3s ease-in-out;
  -webkit-transform: translate(0, 90vh);
          transform: translate(0, 90vh); }

.obstacles-show {
  -webkit-animation: show-obstacle 2s ease-in-out;
          animation: show-obstacle 2s ease-in-out; }

@-webkit-keyframes show-obstacle {
  from {
    top: -20vh; }
  to {
    top: 0; } }

@keyframes show-obstacle {
  from {
    top: -20vh; }
  to {
    top: 0; } }

section.splash-page {
  position: relative;
  height: 100vh;
  text-align: center;
  font-family: "IBM Plex Sans", sans-serif; }
  section.splash-page:before {
    content: "";
    position: absolute;
    width: 200%;
    height: 200%;
    top: -50%;
    left: -50%;
    z-index: -1;
    background: url(/git-commander/static/media/star-background.ceda136c.jpg) 0 0 repeat;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    -webkit-animation: forward 600s linear infinite;
            animation: forward 600s linear infinite; }
  section.splash-page h2 {
    font-family: 'Bungee Outline', cursive;
    letter-spacing: 3px;
    color: white;
    font-size: 70px;
    margin-top: 10%; }
  section.splash-page .octocat-ship {
    width: 20%;
    position: absolute;
    bottom: 25%;
    left: 20%;
    z-index: 10;
    -webkit-animation: upDown 5s alternate infinite;
            animation: upDown 5s alternate infinite; }
  section.splash-page .form-item-container {
    color: white;
    display: -webkit-flex;
    display: flex;
    width: 30%;
    margin: auto;
    -webkit-flex-direction: column;
            flex-direction: column;
    text-align: center; }
    section.splash-page .form-item-container label {
      padding: 10px;
      font-size: 1.2rem; }
    section.splash-page .form-item-container input#userName {
      font-size: 2rem;
      text-align: center;
      height: 50px;
      background-color: #212730;
      color: white;
      font-family: "Source Code Pro", monospace;
      border-radius: 10px 10px 0px 0px; }
    section.splash-page .form-item-container .start-game-btn {
      height: 50px;
      font-size: 1.5rem;
      border-radius: 0px 0px 10px 10px;
      font-weight: bold; }
  section.splash-page div.instructions-cont {
    width: 40%;
    position: absolute;
    top: 35%;
    left: 40%;
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 20px;
    padding: 10px; }
    section.splash-page div.instructions-cont h3 {
      font-size: 2rem; }
    section.splash-page div.instructions-cont h4 {
      font-size: 1.2rem; }
    section.splash-page div.instructions-cont ol {
      text-align: left;
      width: 80%;
      margin: 10px auto;
      font-size: 1.2rem; }

article.challenge-card {
  background-color: #212730; }
  article.challenge-card div {
    color: rgba(89, 255, 0, 0.8);
    font-family: "Source Code Pro", monospace;
    margin: 20px;
    padding: 10px;
    border: 1px dashed blue; }
  article.challenge-card div:first-child {
    font-family: "IBM Plex Sans", sans-serif; }

section.game-window {
  background: url(/git-commander/static/media/star-background.ceda136c.jpg) repeat 0 0;
  -webkit-animation: flow 1000s infinite linear;
          animation: flow 1000s infinite linear;
  position: relative; }
  section.game-window div.obstacle-container {
    position: relative;
    height: 100%; }
    section.game-window div.obstacle-container .obstacle {
      width: 15%;
      margin: 20px;
      position: absolute;
      -webkit-animation: 7s spin infinite linear;
              animation: 7s spin infinite linear;
      transition: -webkit-transform 1s ease-in-out;
      transition: transform 1s ease-in-out;
      transition: transform 1s ease-in-out, -webkit-transform 1s ease-in-out; }
  section.game-window .octo-ship {
    width: 20%;
    position: absolute;
    bottom: 5%;
    transition: -webkit-transform 1.9s ease-in-out;
    transition: transform 1.9s ease-in-out;
    transition: transform 1.9s ease-in-out, -webkit-transform 1.9s ease-in-out; }

div.progress-indicator {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100%;
  width: 10%;
  position: absolute;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  right: 0;
  top: 0; }
  div.progress-indicator .current-round {
    background-color: rgba(89, 255, 0, 0.8); }
  div.progress-indicator span {
    width: 30%;
    border-top: 1px solid #212730;
    height: 30%;
    background-color: rgba(255, 255, 255, 0.7); }
  div.progress-indicator span:first-child,
  div.progress-indicator span:last-child {
    width: 100px;
    padding: 5px;
    height: 45px;
    text-align: center;
    font-size: 1.3rem;
    font-family: "IBM Plex Sans", sans-serif; }
  div.progress-indicator span:first-child {
    border-radius: 0px 0px 0px 20px;
    border: none; }
  div.progress-indicator span:last-child {
    border-radius: 20px 0px 0px 0px; }

aside.user-interface {
  font-family: "IBM Plex Sans", sans-serif;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  background-color: #ededed;
  text-align: center; }
  aside.user-interface h2 {
